import { useState } from "react";
import { useParams, Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Separator } from "@/components/ui/separator";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { 
  ChevronLeft, 
  Eye, 
  Star, 
  MessageCircle,
  TrendingUp,
  Calendar,
  Store,
  Heart,
  ExternalLink,
  Edit
} from "lucide-react";
import { useRestaurant } from "@/hooks/useRestaurants";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { format, subDays, startOfDay, eachDayOfInterval } from "date-fns";
import { 
  LineChart, 
  Line, 
  XAxis, 
  YAxis, 
  CartesianGrid, 
  Tooltip, 
  ResponsiveContainer
} from "recharts";

type TimeRange = "today" | "7days" | "30days" | "all";

const AdminRestaurantStats = () => {
  const { id } = useParams<{ id: string }>();
  const { t } = useTranslation();
  const { localizeRestaurant } = useLocalizedContent();
  const [timeRange, setTimeRange] = useState<TimeRange>("30days");

  const getDateFilter = () => {
    const now = new Date();
    switch (timeRange) {
      case "today":
        return startOfDay(now).toISOString();
      case "7days":
        return startOfDay(subDays(now, 6)).toISOString();
      case "30days":
        return startOfDay(subDays(now, 29)).toISOString();
      default:
        return null;
    }
  };

  const getChartDays = () => {
    switch (timeRange) {
      case "today": return 1;
      case "7days": return 7;
      case "30days": return 30;
      default: return 30;
    }
  };
  
  const { data: restaurant, isLoading: restaurantLoading } = useRestaurant(id!);
  const dateFilter = getDateFilter();

  // Fetch page views for this restaurant
  const { data: pageViews = [], isLoading: viewsLoading } = useQuery({
    queryKey: ["restaurant-page-views", id, timeRange],
    queryFn: async () => {
      let query = supabase
        .from("page_views")
        .select("created_at")
        .eq("page_type", "restaurant")
        .eq("target_id", id!);
      
      if (dateFilter) {
        query = query.gte("created_at", dateFilter);
      }
      
      const { data, error } = await query;
      if (error) throw error;
      return data;
    },
    enabled: !!id,
  });

  // Fetch comments for this restaurant with user profiles
  const { data: comments = [], isLoading: commentsLoading } = useQuery({
    queryKey: ["restaurant-comments", id, timeRange],
    queryFn: async () => {
      let query = supabase
        .from("comments")
        .select("id, user_id, content, rating_value, created_at")
        .eq("target_type", "restaurant")
        .eq("target_id", id!)
        .order("created_at", { ascending: false });
      
      if (dateFilter) {
        query = query.gte("created_at", dateFilter);
      }
      
      const { data, error } = await query;
      if (error) throw error;
      
      if (data && data.length > 0) {
        const userIds = [...new Set(data.map(c => c.user_id))];
        const { data: profiles } = await supabase
          .rpc("get_public_profiles", { user_ids: userIds });
        
        return data.map(c => ({
          ...c,
          profile: profiles?.find((p: any) => p.id === c.user_id)
        }));
      }
      return [];
    },
    enabled: !!id,
  });

  // Fetch favorites for this restaurant
  const { data: favorites = [], isLoading: favoritesLoading } = useQuery({
    queryKey: ["restaurant-favorites", id, timeRange],
    queryFn: async () => {
      let query = supabase
        .from("favorites")
        .select("user_id, created_at")
        .eq("restaurant_id", id!);
      
      if (dateFilter) {
        query = query.gte("created_at", dateFilter);
      }
      
      const { data, error } = await query;
      if (error) throw error;
      
      if (data && data.length > 0) {
        const userIds = data.map(f => f.user_id);
        const { data: profiles } = await supabase
          .rpc("get_public_profiles", { user_ids: userIds });
        
        return data.map(f => ({
          ...f,
          profile: profiles?.find((p: any) => p.id === f.user_id)
        }));
      }
      return [];
    },
    enabled: !!id,
  });

  // Fetch participating activities
  const { data: activities = [] } = useQuery({
    queryKey: ["restaurant-activities", id],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("activity_restaurants")
        .select(`
          activity_id,
          activities (
            id,
            title_zh,
            title_en,
            title_pt,
            status
          )
        `)
        .eq("restaurant_id", id!);
      
      if (error) throw error;
      return data?.map(ar => ar.activities).filter(Boolean) || [];
    },
    enabled: !!id,
  });

  // Generate view trend data
  const viewTrendData = (() => {
    if (timeRange === "all") {
      // For all time, group by day for last 30 days
      const days = 30;
      const endDate = new Date();
      const startDate = subDays(endDate, days - 1);
      const dateRange = eachDayOfInterval({ start: startDate, end: endDate });
      
      return dateRange.map(date => {
        const dateStr = format(date, "yyyy-MM-dd");
        const count = pageViews.filter(v => 
          format(new Date(v.created_at), "yyyy-MM-dd") === dateStr
        ).length;
        return { date: dateStr, views: count };
      });
    } else {
      const days = getChartDays();
      const endDate = new Date();
      const startDate = subDays(endDate, days - 1);
      const dateRange = eachDayOfInterval({ start: startDate, end: endDate });
      
      return dateRange.map(date => {
        const dateStr = format(date, "yyyy-MM-dd");
        const count = pageViews.filter(v => 
          format(new Date(v.created_at), "yyyy-MM-dd") === dateStr
        ).length;
        return { date: dateStr, views: count };
      });
    }
  })();

  // Calculate average rating
  const avgRating = comments.length > 0
    ? comments.filter(c => c.rating_value).reduce((sum, c) => sum + (c.rating_value || 0), 0) / 
      comments.filter(c => c.rating_value).length
    : 0;

  const localizedRestaurant = restaurant ? localizeRestaurant(restaurant) : null;

  if (restaurantLoading) {
    return (
      <div className="space-y-6">
        <Skeleton className="h-8 w-48" />
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {[1, 2, 3, 4].map(i => <Skeleton key={i} className="h-24" />)}
        </div>
        <Skeleton className="h-64" />
      </div>
    );
  }

  if (!restaurant || !localizedRestaurant) {
    return (
      <div className="flex items-center justify-center min-h-[50vh]">
        <p className="text-muted-foreground">{t("error.notFound")}</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div className="flex items-center gap-4">
          <Button variant="ghost" size="icon" asChild>
            <Link to="/admin/restaurant-stats">
              <ChevronLeft className="h-5 w-5" />
            </Link>
          </Button>
          <div className="flex items-center gap-3">
            {restaurant.image_url ? (
              <img 
                src={restaurant.image_url} 
                alt={localizedRestaurant.name}
                className="w-12 h-12 rounded-lg object-cover"
              />
            ) : (
              <div className="w-12 h-12 rounded-lg bg-muted flex items-center justify-center">
                <Store className="h-6 w-6 text-muted-foreground" />
              </div>
            )}
            <div>
              <h1 className="text-2xl font-bold">{localizedRestaurant.name}</h1>
              {restaurant.rating > 0 && (
                <div className="flex items-center gap-1 text-sm text-muted-foreground">
                  <Star className="h-3 w-3 fill-amber-400 text-amber-400" />
                  <span>{restaurant.rating.toFixed(1)}</span>
                </div>
              )}
            </div>
          </div>
        </div>
        <div className="flex items-center gap-2">
          <Calendar className="h-4 w-4 text-muted-foreground" />
          <Select value={timeRange} onValueChange={(v) => setTimeRange(v as TimeRange)}>
            <SelectTrigger className="w-[140px]">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="today">{t('analytics.today', '今日')}</SelectItem>
              <SelectItem value="7days">{t('analytics.last7Days', '近7天')}</SelectItem>
              <SelectItem value="30days">{t('analytics.last30Days', '近30天')}</SelectItem>
              <SelectItem value="all">{t('analytics.allTime', '所有时间')}</SelectItem>
            </SelectContent>
          </Select>
          <Button variant="outline" size="sm" asChild>
            <Link to={`/restaurant/${id}`} target="_blank">
              <ExternalLink className="h-4 w-4 mr-1" />
              {t('admin.preview', '预览')}
            </Link>
          </Button>
          <Button variant="outline" size="sm" asChild>
            <Link to="/admin/restaurants" state={{ editRestaurantId: id }}>
              <Edit className="h-4 w-4 mr-1" />
              {t('admin.edit', '编辑')}
            </Link>
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-blue-500/10 rounded-lg">
                <Eye className="h-5 w-5 text-blue-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.views', '浏览量')}</p>
                <p className="text-2xl font-bold">{pageViews.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-amber-500/10 rounded-lg">
                <MessageCircle className="h-5 w-5 text-amber-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.comments', '评论')}</p>
                <p className="text-2xl font-bold">{comments.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-red-500/10 rounded-lg">
                <Heart className="h-5 w-5 text-red-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.favorites', '收藏')}</p>
                <p className="text-2xl font-bold">{favorites.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-3">
              <div className="p-2 bg-green-500/10 rounded-lg">
                <Star className="h-5 w-5 text-green-500" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">{t('admin.avgRating', '平均评分')}</p>
                <p className="text-2xl font-bold">{avgRating > 0 ? avgRating.toFixed(1) : '-'}</p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* View Trend Chart */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <TrendingUp className="w-5 h-5" />
            {t('admin.viewTrend', '浏览趋势')}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="h-[300px]">
            {viewTrendData.length > 0 ? (
              <ResponsiveContainer width="100%" height="100%">
                <LineChart data={viewTrendData}>
                  <CartesianGrid strokeDasharray="3 3" className="stroke-muted" />
                  <XAxis 
                    dataKey="date" 
                    tickFormatter={(value) => format(new Date(value), "MM/dd")}
                    className="text-xs"
                  />
                  <YAxis className="text-xs" />
                  <Tooltip 
                    labelFormatter={(value) => format(new Date(value), "yyyy-MM-dd")}
                    contentStyle={{ 
                      backgroundColor: "hsl(var(--background))", 
                      border: "1px solid hsl(var(--border))" 
                    }}
                  />
                  <Line 
                    type="monotone" 
                    dataKey="views" 
                    stroke="hsl(var(--primary))" 
                    strokeWidth={2}
                    name={t("admin.views")}
                  />
                </LineChart>
              </ResponsiveContainer>
            ) : (
              <div className="h-full flex items-center justify-center text-muted-foreground">
                {t("analytics.noData")}
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Recent Comments */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <MessageCircle className="w-5 h-5" />
              {t('admin.recentComments', '最近评论')}
            </CardTitle>
            <CardDescription>
              {t('admin.showingRecent', '显示最近 {{count}} 条', { count: Math.min(comments.length, 10) })}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4 max-h-[400px] overflow-y-auto">
              {comments.slice(0, 10).map((comment: any) => (
                <div key={comment.id} className="flex gap-3 p-3 bg-muted/50 rounded-lg">
                  <Avatar className="h-8 w-8">
                    <AvatarImage src={comment.profile?.avatar_url} />
                    <AvatarFallback>
                      {comment.profile?.display_name?.[0] || "?"}
                    </AvatarFallback>
                  </Avatar>
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2 mb-1">
                      <span className="font-medium text-sm">
                        {comment.profile?.display_name || t("common.anonymous")}
                      </span>
                      {comment.rating_value && (
                        <Badge variant="secondary" className="text-xs">
                          <Star className="h-3 w-3 mr-1 fill-amber-400 text-amber-400" />
                          {comment.rating_value}
                        </Badge>
                      )}
                    </div>
                    <p className="text-sm text-muted-foreground line-clamp-2">{comment.content}</p>
                    <p className="text-xs text-muted-foreground mt-1">
                      {format(new Date(comment.created_at), "yyyy-MM-dd HH:mm")}
                    </p>
                  </div>
                </div>
              ))}
              {comments.length === 0 && (
                <p className="text-center text-muted-foreground py-8">
                  {t("admin.noComments", "暂无评论")}
                </p>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Recent Favorites */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Heart className="w-5 h-5" />
              {t('admin.recentFavorites', '最近收藏')}
            </CardTitle>
            <CardDescription>
              {t('admin.showingRecent', '显示最近 {{count}} 条', { count: Math.min(favorites.length, 10) })}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-3 max-h-[400px] overflow-y-auto">
              {favorites.slice(0, 10).map((favorite: any, index: number) => (
                <div key={index} className="flex items-center gap-3 p-3 bg-muted/50 rounded-lg">
                  <Avatar className="h-8 w-8">
                    <AvatarImage src={favorite.profile?.avatar_url} />
                    <AvatarFallback>
                      {favorite.profile?.display_name?.[0] || "?"}
                    </AvatarFallback>
                  </Avatar>
                  <div className="flex-1 min-w-0">
                    <span className="font-medium text-sm">
                      {favorite.profile?.display_name || t("common.anonymous")}
                    </span>
                    <p className="text-xs text-muted-foreground">
                      {format(new Date(favorite.created_at), "yyyy-MM-dd HH:mm")}
                    </p>
                  </div>
                </div>
              ))}
              {favorites.length === 0 && (
                <p className="text-center text-muted-foreground py-8">
                  {t("admin.noFavorites", "暂无收藏")}
                </p>
              )}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Participating Activities */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Calendar className="w-5 h-5" />
            {t('admin.participatingActivities', '参与的活动')}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {activities.map((activity: any) => (
              <div 
                key={activity.id} 
                className="p-4 border rounded-lg bg-card hover:shadow-md transition-shadow"
              >
                <div className="flex items-center justify-between mb-2">
                  <h4 className="font-medium line-clamp-1">
                    {activity.title_zh || activity.title_en}
                  </h4>
                  <Badge variant={activity.status === 'published' ? 'default' : 'secondary'}>
                    {activity.status === 'published' ? t('admin.published', '已发布') : t('admin.draft', '草稿')}
                  </Badge>
                </div>
                <Button variant="outline" size="sm" className="w-full" asChild>
                  <Link to={`/admin/activity-stats/${activity.id}`}>
                    {t('admin.viewStats', '查看统计')}
                  </Link>
                </Button>
              </div>
            ))}
            {activities.length === 0 && (
              <p className="text-muted-foreground col-span-full text-center py-8">
                {t("admin.noActivities", "暂未参与活动")}
              </p>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default AdminRestaurantStats;
